<template>
  <input :disabled="!enabled" type="datetime-local"  :id="pickerId" class="form-control" v-on:change="selectTime"/>
</template>

<script>
import $ from 'jquery'
import utils from '@/utils/utils'

export default {
  name: 'DateTimePicker2',
  data () {
    return {
      pickerId: utils.randomId('dtPickerId'),
      datetime: '2022-07-29T20:25:00'
    }
  },
  props: {
    enabled: {
      type: Boolean,
      required: false,
      default: true
    },
    modelValue: {
      type: String,
      required: true
    },
  },
  emits: ['update:modelValue'],
  components: {
  },
  computed: {
  },
  methods: {
    selectTime() {
      this.datetime = $(`#${this.pickerId}`).val()
      // console.log('selectTime', this.datetime)
    }

  },
  watch: {
    modelValue (value) {
      this.datetime = value
    },
    datetime (value, valueOld) {
      if (value && value !== valueOld) {
        $(`#${this.pickerId}`).val(value)
        this.$emit('update:modelValue', value)
      }
    }
  },
  mounted () {
    this.datetime = this.modelValue
  }
}
</script>
