<template>
  <!-- <date-picker v-model="date" v-on:dp-change="$emit('input', date)" :config="options"></date-picker> -->
  <!-- <date-picker v-model="date" :config="options"></date-picker> -->
  <div class="btn-group btn-group-toggle mr-4" data-toggle="buttons">
    <label class="btn btn-primary active">
      <input type="radio" name="options" value="today" v-model="rangeSelection"> 
    </label>
    <label class="btn btn-primary">
      <input type="radio" name="options" value="7-day" v-model="rangeSelection"> 7
    </label>
    <label class="btn btn-primary">
      <input type="radio" name="options" value="30-day" v-model="rangeSelection"> 30
    </label>
    <label class="btn btn-primary">
      <input type="radio" name="options" value="all" v-model="rangeSelection"> 
    </label>
    <label class="btn btn-primary">
      <input type="radio" name="options" value="custom" v-model="rangeSelection"> 
    </label>
  </div>
  <div>
    <DateTimePicker2 v-model="dateTimeStart" :enabled="dateTimeEnabled"></DateTimePicker2>
  </div>
  <div>-</div>
  <div>
    <DateTimePicker2 v-model="dateTimeEnd" :enabled="dateTimeEnabled"></DateTimePicker2>
  </div>
</template>

<script>
// import $ from 'jquery'
import DateTimePicker2 from '@/components/utils/DateTimePicker2'
import dayjs from 'dayjs'

export default {
  name: 'DateTimePicker',
  data () {
    return {
      rangeSelection: 'today',
      dateTimeStart: dayjs(new Date(new Date(new Date().setHours(0)).setMinutes(0))).format('YYYY-MM-DDTHH:mm:ss'),
      dateTimeEnd: dayjs().format('YYYY-MM-DDTHH:mm:ss'),
      dateTimeEnabled: false
    }
  },
  props: {
    modelValue: {
      type: String,
      required: false,
      default: ''
    },
  },
  emits: ['update:modelValue'],
  watch: {
    rangeSelection () {
      // console.log('rangeSelection', this.rangeSelection)
      this.dateTimeEnd = dayjs().format('YYYY-MM-DDTHH:mm:ss')
      // new Date(new Date().setDate(new Date().getDate() -30))
      if (this.rangeSelection=='today') {
        this.dateTimeEnabled = false
        this.dateTimeStart = dayjs(new Date(new Date(new Date().setHours(0)).setMinutes(0))).format('YYYY-MM-DDTHH:mm:ss')
      } else if (this.rangeSelection=='7-day') {
        this.dateTimeEnabled = false
        this.dateTimeStart = dayjs(new Date(new Date().setDate(new Date().getDate()-7))).format('YYYY-MM-DDTHH:mm:ss')
      } else if (this.rangeSelection=='30-day') {
        this.dateTimeEnabled = false
        this.dateTimeStart = dayjs(new Date(new Date().setDate(new Date().getDate()-30))).format('YYYY-MM-DDTHH:mm:ss')
      } else if (this.rangeSelection=='all') {
        this.dateTimeEnabled = false
        // <input type="datetime-local">"YYYY-MM-DDTHH:mm", 0"YYYY-MM-DDTHH:mm:ss"
        this.dateTimeStart = dayjs(new Date('2020-01-01T00:00:01')).format('YYYY-MM-DDTHH:mm:ss')
      } else if (this.rangeSelection=='custom') {
        this.dateTimeEnabled = true
      }
    },
    dateTimeStart () {
      // console.log('dateTimeStart', value)
      this.$emit('update:modelValue', `${this.dateTimeStart};${this.dateTimeEnd}`)
    },
    dateTimeEnd () {
      // console.log('dateTimeEnd', value)
      this.$emit('update:modelValue', `${this.dateTimeStart};${this.dateTimeEnd}`)
    },
    modelValue (value) {
      if (value) {
        let datetimes = value.split(';')
        this.dateTimeStart = datetimes[0]
        this.dateTimeEnd = datetimes[1]
      }
    }
  },
  components: {
    DateTimePicker2,
  },
  computed: {
  },
  methods: {
  },
  mounted () {
  },
  getDefaultRange() {
    // 24
    return [
      dayjs(new Date(new Date(new Date().setHours(0)).setMinutes(0))).format('YYYY-MM-DDTHH:mm:ss'),
      dayjs().format('YYYY-MM-DDTHH:mm:ss'),
    ].join(';')
  },
  rangeToFilter(dateTimeRange) {
    let datetimes = dateTimeRange.split(';')
    return {
      'date_time__gte': datetimes[0]+'+0800',
      'date_time__lte': datetimes[1]+'+0800'
    }
  }
}
</script>
