<template>
  <div>
    <span :class="'field-label ' + (required ? 'field-required' : '')" v-if="label">{{label}}:</span>
    <select v-model="selected">
      <option v-for="(item, index) in itemList" :key="uniqueId+index" :value="valueType === 'item' ? item : index">{{showType === "field" ? item[showField] : item}}</option>
    </select>
  </div>
</template>

<script>
import utils from '@/utils/utils'

export default {
  name: 'SimpleSelect',
  components: {
  },
  data () {
    return {
      uniqueId: utils.randomString(),
      selected: null
    }
  },
  props: {
    label: {
      type: String,
      required: true
    },
    modelValue: {
      // type: Number,
      required: false,
      default: -1
    },
    showType: {
      type: String,
      required: false,
      default: 'item' // item: show the item itself; field: show a field in item
    },
    valueType: {
      type: String,
      required: false,
      default: 'index' // 'index': the value is index; 'item': the value is item itself
    },
    showField: { // If showType === 'field', show this field
      type: String,
      required: false,
      default: ''
    },
    itemList: {
      type: Array,
      required: true
    },
    required: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  emits: [
    'update:modelValue'
  ],
  computed: {
  },
  methods: {
  },
  watch: {
    modelValue () {
      this.selected = this.modelValue
    },
    selected () {
      this.$emit('update:modelValue', this.selected)
    }
  },
  mounted () {
    this.selected = this.modelValue
  },
  unmounted () {
  },
  created () {
  }
}
</script>
