<template>
  <v-chart class="type-bar-chart" :option="option" />
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart, BarChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart from "vue-echarts";
// import { THEME_KEY } from "vue-echarts";
import { ref, defineComponent } from "vue";
import utils from '@/utils/utils'
import apis from '@/apis/apis'

use([
  CanvasRenderer,
  PieChart,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

export default defineComponent({
  name: "AlertTypeStatsChartBar",
  components: {
    VChart
  },
  provide: {
    // [THEME_KEY]: "dark"
  },
  props: {
    dateTimeFilter: {
      type: Object,
      required: false,
      default: null
    }
  },
  setup() {
    const option = ref({
      title: {
        show: false,
        text: "",
        left: "center"
      },
      tooltip: {
        show: true,
        trigger: "axis",
        formatter: "{b} : {c}"
      },
      legend: {
        show: false,
        orient: "vertical",
        left: "left",
        // data: ["Direct", "Email", "Ad Networks", "Video Ads", "Search Engines"]
      },
      xAxis: {
        type: 'category',
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          data: [120, 200, 150, 80, 70, 110, 130],
          type: 'bar',
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    });

    return { option };
  },
  methods: {
    loadData() {
      let endpoint = `alert/alerts2/groupby/`
      let params = {
        by: `algorithm__name_ch`,
        count: 'id'
      }
      if (this.dateTimeFilter) {
        params = {...params, ...this.dateTimeFilter}
      }
      apis.apiAjaxGet(endpoint, params,
        (res) => {
          if (res.code === 0 && res.data) {
            this.option.series[0].data = res.data.map(v=>v.count_id)
            this.option.xAxis.data = res.data.map(v=>v.algorithm__name_ch)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    dateTimeFilter() {
      // console.log('dateTimeFilter changed', this.dateTimeFilter)
      this.loadData()
    }
  },
  mounted() {
    this.loadData()
  }
});
</script>

<style scoped>
.type-bar-chart {
  /* height: 100vh; */
  height: 100%;
  width: 100%;
}
</style>
