<template>
  <div>
    <v-chart class="channel-stats-chart" :option="option" />
  </div>
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
  ToolboxComponent
} from "echarts/components";
import VChart from "vue-echarts";
// import { THEME_KEY } from "vue-echarts";
import { ref, defineComponent } from "vue";
import utils from '@/utils/utils'
import apis from '@/apis/apis'
import dayjs from 'dayjs'

use([
  CanvasRenderer,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  DataZoomComponent,
  ToolboxComponent 
]);

export default defineComponent({
  name: "AlertChannelStatsChart",
  components: {
    VChart
  },
  provide: {
    // [THEME_KEY]: "dark"
  },
  props: {
    timeUnit: {
      type: Number,
      required: false,
      default: 0
    },
    dateTimeFilter: {
      type: Object,
      required: false,
      default: null
    }
  },
  setup() {
    const option = ref({
      title: {
        show: false,
        text: 'Temperature Change in the Coming Week'
      },
      toolbox: {
        show: true,
        feature: {
          magicType: { type: ['line', 'bar'] },
        },
        right: 40
      },
      tooltip: {
        show: true,
        trigger: "axis",
        // formatter: "{b} : {c} ({d}%)"
      },
      legend: {
        show: true
      },
      grid: {
        left: 70,
        right: 50,
        top: 30,
        bottom: 40
      },
      dataZoom: [
        {
          type: 'inside',
          start: 50,
          end: 100
        },
        {
          top: '85%',
          bottom: '5%',
          start: 50,
          end: 100
        }
      ],
      // toolbox: {
      //   show: true,
      //   feature: {
      //     dataZoom: {
      //       yAxisIndex: 'none'
      //     },
      //     dataView: { readOnly: false },
      //     magicType: { type: ['line', 'bar'] },
      //     restore: {},
      //     saveAsImage: {}
      //   }
      // },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      },
      yAxis: {
        type: 'value',
        // axisLabel: {
        //   formatter: '{value} C'
        // }
      },
      series: [
        {
          name: 'Highest',
          type: 'line',
          data: [10, 11, 13, 11, 12, 12, 9],
          markPoint: {
            data: [
              { type: 'max', name: 'Max' },
              { type: 'min', name: 'Min' }
            ]
          },
          markLine: {
            data: [{ type: 'average', name: 'Avg' }]
          }
        },
        {
          name: 'Lowest',
          type: 'line',
          data: [1, -2, 2, 5, 3, 2, 0],
          markPoint: {
            data: [{ name: '', value: -2, xAxis: 1, yAxis: -1.5 }]
          },
          markLine: {
            data: [
              { type: 'average', name: 'Avg' },
              [
                {
                  symbol: 'none',
                  x: '90%',
                  yAxis: 'max'
                },
                {
                  symbol: 'circle',
                  label: {
                    position: 'start',
                    formatter: 'Max'
                  },
                  type: 'max',
                  name: ''
                }
              ]
            ]
          }
        }
      ]
    });

    return { option };
  },
  watch: {
    timeUnit() {
      // console.log('timeUnit changed', this.timeUnit)
      this.loadData()
    },
    dateTimeFilter() {
      // console.log('dateTimeFilter changed', this.dateTimeFilter)
      this.loadData()
    }
  },
  methods: {
    loadData() {
      let timeUnit = this.timeUnit == 0 ? 'hour' : 'day'
      // setTimeout(()=>{this.option.title.text='Hello world'}, 5000)
      let endpoint = `alert/alerts2/groupby/`
      let params = {
        by: `channel__cno,${timeUnit}`,
        count: 'id'
      }
      if (this.dateTimeFilter) {
        params = {...params, ...this.dateTimeFilter}
      }
      apis.apiAjaxGet(endpoint, params,
        (res) => {
          if (res.code === 0 && res.data) {
            // channel no
            let times = [...new Set(res.data.map(v=>v[timeUnit]))].sort()
            // console.log('times', times)
            let cnos = [...new Set(res.data.map(v=>v.channel__cno))].sort()
            // console.log('cnos', cnos)
            let values = {}
            // 0
            cnos.forEach(v=>{values[v] = new Array(times.length).fill(0)})

            res.data.forEach(v=>{
              let index = times.indexOf(v[timeUnit])
              if (index >= 0) {
                values[v.channel__cno][index] = v.count_id
              }
            })
            // UTC
            times = times.map(v=>{
              let dt = new Date(v)
              if (this.timeUnit == 0) {
                return dayjs(dt).format('YYYY-MM-DDTHH:00')
              } else {
                return dayjs(dt).format('YYYY-MM-DD')
              }
            })

            // 10
            let zoomStart
            if (times.length <= 10) {
              zoomStart = 0
            } else {
              zoomStart = (times.length - 10) * 100 / times.length
            }
            this.option.dataZoom[0].start = zoomStart
            this.option.dataZoom[1].start = zoomStart
            this.option.dataZoom[0].end = 100
            this.option.dataZoom[1].end = 100

            this.option.xAxis.data = times
            this.option.series = []
            for (let cno in values) {
              this.option.series.push({
                name: `-${cno}`,
                type: 'line',
                data: values[cno],
              })
            }
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  mounted() {
    this.loadData()
  }
});
</script>

<style scoped>
.channel-stats-chart {
  /* height: 100vh; */
  height: 160px;
  /* width: 100%; */
}
</style>
