<template>
  <div>
    <SimpleSelect
      v-model="value"
      :itemList="itemList"
      :label="label"
    ></SimpleSelect>
  </div>
</template>

<script>
/**
 * This is used to show an options select based on database group-by categories
 */
import utils from '@/utils/utils'
import SimpleSelect from '@/components/utils/SimpleSelect'
import apis from '@/apis/apis'

export default {
  name: 'DbSelectFilter',
  components: {
    SimpleSelect
  },
  data () {
    return {
      value: -1,
      itemList: []
    }
  },
  props: {
    endpoint: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    modelValue: {
      type: String
    },
    field: {
      type: String,
      required: true
    },
    descent: {
      // Default false to order ascending
      type: Boolean,
      required: false,
      default: false
    },
    defaultIndex: {
      // If true, default value will be set to the index
      type: Number,
      require: false,
      default: -1
    },
    nullItemText: {
      type: String,
      required: false,
      default: ''
    }
  },
  emits: [
    'update:modelValue'
  ],
  watch: {
    modelValue (value) {
      this.value = this.fromInitValue(value)
    },
    value () {
      this.emitEvent()
    }
  },
  methods: {
    emitEvent () {
      let value = ''

      // Convert Number value to String value
      if (this.value > 0) {
        value = this.itemList[this.value]
      }

      return this.$emit('update:modelValue', value)
    },
    fromInitValue (modelValue) {
      // Convert String init value to Number value
      if (!modelValue) {
        return 0
      }
      return this.itemList.indexOf(modelValue)
    },
    getOptionList () {
      let field = this.field

      // Get field values list through sources/goten/skuwarehouses/groupby/
      let params = {
        limit: 500,
        by: field,
        count: 'id',
        ordering: `${this.descent ? '-' : ''}${field}`
      }
      apis.apiAjaxGet(this.endpoint, params,
        (res) => {
          // console.log('loadData success', res.data.data)
          // Returned value is in this format
          // {
          //     "code": 0,
          //     "message": "Success",
          //     "data": {
          //         "count": 6,
          //         "next": null,
          //         "previous": null,
          //         "results": [
          //             {
          //                 "warehouse": "CN",
          //                 "count_id": 1675
          //             },
          //         .....

          // Convert result to simple array.
          // some field is Number, so it's necessary to convert to string
          this.itemList = res.data.results.map(v => (v[field] && v[field].toString()))
          this.itemList.unshift(this.nullItemText) // Add an empty string to disable filtering
          // Set default value
          if (this.defaultIndex < this.itemList.length) {
            this.value = this.defaultIndex + 1
          }
          // console.log('getOptionList this.itemList: ', params, this.itemList)
        },
        utils.defaultApiErrorHandler(this.endpoint, `loadData failed`),
        () => {
          // finalCb
        },
        {
          // config
        },
        true // wantCache
      )
    }
  },
  mounted () {
    this.getOptionList()
    this.value = this.fromInitValue(this.modelValue)
  }
}
</script>
