<template>
              <div class="row w-100 ml-1 py-2 border-bottom">
                <label class="mt-1 field-label"></label>
                <span class="mt-1 mr-4">{{algDisplayName}}</span>
                <label class="mt-1 field-label" for="id-channel-alg-config"></label>
                <input class="ml-1" type="radio" id="id-channel-alg-config" name="channelAlgConfig" v-model="status.configured" value="1">
                <label class="ml-2 mt-1 field-label" for="id-channel-alg-unconfig"></label>
                <input class="ml-2" type="radio" id="id-channel-alg-unconfig" name="channelAlgConfig" v-model="status.configured" value="0">
              </div>
              <div v-show="status.configured === '1'" class="row mt-2 pb-2 border-bottom">
                <span class="field-label pl-3"></span>
                <div class="container-fluid">
                  <div class="row">
                    <div class="col-3 text-right">
                      <span class="">()</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="status.analyze_interval" type="number" step="500" />
                      <span class="">(5001000)</span>
                    </div>
                  </div>
                  <div class="row mt-1">
                    <div class="col-3 text-right">
                      <span class="">()</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="status.alert_interval" type="number" step="1" />
                    </div>
                  </div>
                  <div class="row mt-1">
                    <div class="col-3 text-right">
                      <span class="">(0-1)</span>
                    </div>
                    <div class="col">
                      <input class="ml-2" v-model.number="status.alert_threshold" type="number" step="0.05" />
                      <span class="">()</span>
                    </div>
                  </div>
                </div>
              </div>

              <div v-show="status.configured === '1'" class="row mt-2 pb-2 border-bottom">
                <div class="container-fluid">
                  <div class="row mb-1">
                    <span class="field-label pl-3 pt-1"></span>
                    <a class="btn-sm btn-primary" data-toggle="collapse" href="#dateTimeSetting" role="button" aria-expanded="false" aria-controls="dateTimeSetting">{{dateTimeSettingShown ? '' : ''}}</a>
                  </div>
                  <div class="row">
                    <div class="col collapse" id="dateTimeSetting">
                      <div class="border w-100" style="height: 350px;">
                        <WeekDatetimePicker v-model="status.alert_times"></WeekDatetimePicker>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              
              <div v-show="status.configured === '1'" class="row mt-2 border-bottom">
                <div class="container-fluid">
                  <div class="row">
                    <span class="field-label py-2 pl-3">(ROI)</span>
                    <button class="btn btn-sm btn-primary" role="button" @click="status.roi_region=null">()</button>
                  </div>
                  <div class="row mt-2">
                    <div class="col">
                      <RoiDrawPanel :imgSrc="channelImage" :panelShown="modalShown && status.configured == '1'" v-model="status.roi_region"></RoiDrawPanel>
                    </div>
                  </div>
                </div>
              </div>
</template>

<script>
import WeekDatetimePicker from '../utils/WeekDatetimePicker'
import RoiDrawPanel from './RoiDrawPanel'

export default {
  name: 'ChannelAlgConfigDlg',
  components: {
    WeekDatetimePicker,
    RoiDrawPanel
  },
  data () {
    return {
      dateTimeSettingShown: false,
      status: {},
    }
  },
  props: {
    channelImage: {
      type: String,
      required: false,
      default: ''
    },
    modalShown: {
      type: Boolean,
      required: false,
      default: false
    },
    algDisplayName: {
      type: String,
      required: true
    },
    modelValue: {
      type: Object,
      required: true
    },
  },
  emits: ['update:modelValue'],
  computed: {
  },
  methods: {
  },
  watch: {
    modelValue(value) {
      if (JSON.stringify(this.status) !== JSON.stringify(value)) {
        this.status = {...this.modelValue}
      }
    },
    status: {
      handler() {
        // Emit message only when value really changed
        this.$emit('update:modelValue', this.status)
      },
      deep: true
    }
  },
  mounted () {
  }
}
</script>
