<template>
  <v-chart class="disk-usage-chart" :option="option" />
</template>

<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { GaugeChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from "echarts/components";
import VChart from "vue-echarts";
// import { THEME_KEY } from "vue-echarts";
import { ref, defineComponent } from "vue";
import utils from '@/utils/utils'
import apis from '@/apis/apis'

use([
  CanvasRenderer,
  GaugeChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent
]);

export default defineComponent({
  name: "DiskUsageChart",
  components: {
    VChart
  },
  provide: {
    // [THEME_KEY]: "dark"
  },
  setup() {
    const option = ref({
      series: [
        {
          type: 'gauge',
          progress: {
            show: true,
            width: 18
          },
          axisLine: {
            lineStyle: {
              width: 18
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            length: 15,
            lineStyle: {
              width: 2,
              color: '#999'
            }
          },
          axisLabel: {
            distance: 25,
            color: '#999',
            fontSize: 10
          },
          anchor: {
            show: true,
            showAbove: true,
            size: 25,
            itemStyle: {
              borderWidth: 10
            }
          },
          title: {
            show: false
          },
          detail: {
            valueAnimation: true,
            fontSize: 20,
            offsetCenter: [0, '70%']
          },
          data: [
            {
              value: 70
            }
          ]
        }
      ]
    });

    return { option };
  },
  mounted() {
    // setTimeout(()=>{this.option.title.text='Hello world'}, 5000)
    let endpoint = 'alert/alerts2/groupby/?by=algorithm__name_ch&count=id'
    apis.apiAjaxGet(endpoint, {},
      (res) => {
        if (res.code === 0 && res.data) {
          // let data = res.data.map(v=>{return {value: v.count_id, name: v.algorithm__name_ch}})
          // this.option.series[0].data = data
        }
      },
      utils.defaultApiErrorHandler(endpoint, ``)
    )
  }
});
</script>

<style scoped>
.disk-usage-chart {
  /* height: 100vh; */
  height: 100%;
  width: 100%;
}
</style>
