<template>
  <div id="roiImageBox" class="w-100 roi-preview-image">
    <img :id="roiImageId" :class="roiImgClass" :src="imgSrc" alt="">
    <canvas
      :id="canvasId"
      :width="canvasWidth"
      :height="canvasHeight"
      :class="roiCanvasClass"
      @mousedown="onMouseDown($event)"
      @mousemove="onMouseMove($event)"
      @mouseup="onMouseUp()"
      @click="onClick($event)"
      @dblclick="onDoubleClick($event)">
    </canvas>
  </div>
</template>

<script>
// const { loadImage } = require('canvas')
import utils from '@/utils/utils'
// import $ from 'jquery'

export default {
  name: 'RoiDrawPanel',
  components: {
  },
  data () {
    return {
      canvasId: utils.randomString(),
      roiImageId: utils.randomString(),
      roiImgClass: 'roiImgClassNormal',
      roiCanvasClass: 'roiCanvasClassNormal',
      canvasWidth: 1,
      canvasHeight: 1,
      roiBoxes: [], // canvas
      // 
      mouseIsDown: false,
      mouseDownX: 0,
      mouseDownY: 0,
      mouseCurrX: 0,
      mouseCurrY: 0,
    }
  },
  props: {
    imgSrc: {
      type: String,
      required: false,
      default: '',
    },
    panelShown: {
      type: Boolean,
      required: false,
      default: false
    },
    modelValue: {
      required: true
    },
  },
  emits: ['update:modelValue'],
  computed: {
  },
  methods: {
    canvasBoxToImageBox() {
      // canvasboximage
    },
    imageBoxToCanvasBox() {
      // imageboxcanvas
    },
    imageLoaded() {
      let img=document.getElementById(this.roiImageId);
      console.log('Image loaded', this.imgSrc, img.width, img.height, img.clientWidth, img.clientHeight, img.naturalWidth, img.naturalHeight, img.getClientRects())

      if (this.panelShown && !img.naturalWidth) {
        setTimeout(() => this.imageLoaded(), 300)
        return
      }

      this.canvasWidth = img.width
      this.canvasHeight = img.height

      // canvas
      this.loadRoiBoxes(this.modelValue)

      setTimeout(() => {
        // 
        this.drawRois()
      }, 200)
    },
    drawBox(octx, x1, y1, x2, y2) {
      octx.fillStyle = 'rgba(30, 30, 200, 0.2)'
      octx.fillRect(x1, y1, x2 - x1, y2 - y1)
      octx.strokeStyle = 'rgba(30, 30, 200, 1)'
      octx.strokeRect(x1, y1, x2 - x1, y2 - y1)
    },
    drawRois() {
      let canvas = document.getElementById(this.canvasId)
      // Create an offscreen canvas
      let oc = document.createElement('canvas')
      oc.width = canvas.width
      oc.height = canvas.height
      let octx = oc.getContext('2d')

      if (this.mouseIsDown && this.mouseDownX !== this.mouseCurrX && this.mouseDownY !== this.mouseCurrY) {
        this.drawBox(octx, this.mouseDownX, this.mouseDownY, this.mouseCurrX, this.mouseCurrY)
      }

      this.roiBoxes.forEach(v=>{
        this.drawBox(octx, v[0], v[1], v[2], v[3])
      })

      // Then copy from offscreen to on screen
      let imgData = octx.getImageData(0, 0, oc.width, oc.height)
      let ctx = canvas.getContext('2d')
      ctx.putImageData(imgData, 0, 0)
    },
    filterRoiBoxes() {
      // 
      let filteredResults = []
      for (let i in this.roiBoxes) {
        let v1 = this.roiBoxes[i]
        let discard = false
        for (let j in this.roiBoxes) {
          if (i !== j) {
            let v2 = this.roiBoxes[j]
            // v1v2
            if (v1[0] >= v2[0] && v1[1] >= v2[1] && v1[2] <= v2[2] && v1[3] <= v2[3]) {
              discard = true
              break
            }
          }
        }
        if (!discard) {
          filteredResults.push(v1)
        }
      }
      this.roiBoxes = filteredResults
    },
    roiBoxesToEvent() {

      if (!this.roiBoxes.length) {
        return null
      }

      // roiBoxes: canvas->image
      let img=document.getElementById(this.roiImageId);
      let naturalWidth = img.naturalWidth
      let naturalHeight = img.naturalHeight

      let resultBoxes = this.roiBoxes.map(v => {
        return {
          x1: Math.round(v[0] * naturalWidth / this.canvasWidth),
          y1: Math.round(v[1] * naturalHeight / this.canvasHeight),
          x2: Math.round(v[2] * naturalWidth / this.canvasWidth),
          y2: Math.round(v[3] * naturalHeight / this.canvasHeight)
        }
      })

      console.log('roiBoxesToEvent', resultBoxes)

      return JSON.stringify(resultBoxes)
    },
    loadRoiBoxes(modelValue) {
      if (!modelValue) {
        this.roiBoxes = []
        return
      }
      // roiBoxesToEvent
      let img=document.getElementById(this.roiImageId);
      let naturalWidth = img.naturalWidth ? img.naturalWidth : 1
      let naturalHeight = img.naturalHeight ? img.naturalHeight : 1

      let roiBoxes = JSON.parse(modelValue)

      this.roiBoxes = roiBoxes.map(v => {
        return [
          Math.round(v.x1 * this.canvasWidth / naturalWidth),
          Math.round(v.y1 * this.canvasHeight / naturalHeight),
          Math.round(v.x2 * this.canvasWidth / naturalWidth),
          Math.round(v.y2 * this.canvasHeight / naturalHeight),
        ]
      })

      console.log('loadRoiBoxes', this.roiBoxes)
    },
    onMouseDown($event) {
      console.log('onMouseDown', $event)
      if ($event.buttons == 1) {
        this.mouseIsDown = true
        this.mouseCurrX = this.mouseDownX = $event.offsetX
        this.mouseCurrY = this.mouseDownY = $event.offsetY
      }
    },
    onMouseMove($event) {
      // console.log('onMouseMove', $event)
      if (this.mouseIsDown) {
        this.mouseCurrX = $event.offsetX
        this.mouseCurrY = $event.offsetY
        this.drawRois()
      }
    },
    onMouseUp($event) {
      console.log('onMouseUp', $event)
      if (this.mouseIsDown) {
        this.mouseIsDown = false
        // 
        if (this.mouseDownX !== this.mouseCurrX && this.mouseDownY !== this.mouseCurrY) {
          this.roiBoxes.push([
            Math.min(this.mouseDownX, this.mouseCurrX),
            Math.min(this.mouseDownY, this.mouseCurrY),
            Math.max(this.mouseDownX, this.mouseCurrX),
            Math.max(this.mouseDownY, this.mouseCurrY)
          ])

          this.filterRoiBoxes()

          this.drawRois()

          this.$emit('update:modelValue', this.roiBoxesToEvent())
        }
      }
    },
    onClick($event) {
      console.log('onClick', $event)
    },
    onDoubleClick($event) {
      console.log('onDoubleClick', $event)
    },
  },
  watch: {
    panelShown () {
      if (this.panelShown) {
        // canvas
        setTimeout(() => this.imageLoaded(), 500)
      }
    },
    modelValue() {
      console.log('roi modelValue changed', this.modelValue)
      // 
      this.loadRoiBoxes(this.modelValue)
      this.drawRois()
    }
  },
  mounted () {
  }
}
</script>

<style>
.roi-preview-image {
  height: 400px;
  max-width: 100%;
  display: relative;
}
.roiCanvasClassNormal {
  display: '';
  position: absolute;
}
/* .roiCanvasClassHidden {
  display: none;
} */
.roiImgClassNormal {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
}
/* .roiImgClassHidden {
  display: none;
} */
</style>