<template>
  <div class="container-fluid">
    <div class="row w-100 ml-1 my-2">
      <div class="row ml-1 my-2">
        <button class="btn btn-primary" data-toggle="modal" :data-target="'#' + channelEditDlgId"></button>
        <button class="btn btn-light ml-4" :disabled="true"> {{usedNos.length}}/{{availNos.length}}</button>
      </div>
      <div class="row w-100">
        <ApiDataTable
          :handlers="tableHandlers"
          tableId="channelListTableId"
          :dataHeight=400
          :showWaitingDlg='false'
          :showSearch="false"
          :detailView="false"
        ></ApiDataTable>
      </div>
      <ChannelEditDlg
        :modalId="channelEditDlgId"
        :channelId="currRowChannelId"
        :availNos="availNos"
        @channel-saved="onChannelSaved"
      ></ChannelEditDlg>
      <ChannelDeleteDlg
        :modalId="channelDeleteDlgId"
        :channelId="currRowChannelId"
        :cno="currRowChannelNo"
        @channel-deleted="onChannelDeleted"
      ></ChannelDeleteDlg>
      <ChannelAlgConfigDlg
        :modalId="channelAlgConfigDlgId"
        :channelId="currRowChannelId"
        :cno="currRowChannelNo"
        :channelName="currRowChannelName"
        @channel-deleted="onChannelAlgSaved"
      ></ChannelAlgConfigDlg>
    </div>
  </div>
</template>

<script>

import ApiDataTable from '../utils/ApiDataTable'
import utils from '@/utils/utils'
import $ from 'jquery'
import ChannelEditDlg from './ChannelEditDlg'
import ChannelDeleteDlg from './ChannelDeleteDlg'
import ChannelAlgConfigDlg from './ChannelAlgConfigDlg'
import apis from '@/apis/apis'

/**
 * 
 * @param {*} row : row data
 */
window.channelRowSelected = function (channelId, channelNo, channelName) {
  window.currRowChannelId = channelId
  window.currRowChannelNo = channelNo
  window.currRowChannelName = channelName
}

export default {
  name: 'ModuleChConfig',
  components: {
    ApiDataTable,
    ChannelEditDlg,
    ChannelDeleteDlg,
    ChannelAlgConfigDlg
  },
  data () {
    return {
      channelListTableId: utils.randomId('tableId'),
      channelEditDlgId: utils.randomId('modalId'),
      channelDeleteDlgId: utils.randomId('modalId'),
      channelAlgConfigDlgId: utils.randomId('modalId'),
      tableComp: null,
      // For edit
      currRowChannelId: 0,
      currRowChannelNo: 0,
      currRowChannelName: '',

      freeNos: [1, 2, 3, 4, 5, 6, 7, 8],
      usedNos: [],
    }
  },
  computed: {
    tableHandlers () {
      return {
        setComponent: this.tableSetComponent,
        getEndpoint: this.tableGetEndPoint,
        getParams: this.tableGetParams,
        tableRowFormatter: this.tableRowFormatter,
        getColSpecs: this.tableGetColSpecs,
        rowStyle: this.rowStyle,
        getDataList: this.getDataList,
        onPageLoaded: this.onPageLoaded,
      }
    },
    availNos () {
      let nos = [...this.freeNos, ...this.usedNos]
      return nos.sort()
    }
  },
  methods: {
    onChannelSaved () {
      this.reloadTable()
      this.getUsedFreeChannels()
    },
    onChannelDeleted () {
      this.reloadTable()
      this.getUsedFreeChannels()
    },
    onChannelAlgSaved () {
    },
    tableSetComponent (tableComp) {
      this.tableComp = tableComp
    },
    tableRowFormatter (r) {
      return {
        id: r.id,
        cno: r.cno,
        cid: r.cid,
        name: r.name,
        site: r.site,
        url: r.url,
        status: '...',
        operation: `
          <a href="javascript:channelRowSelected(${r.id}, ${r.cno}, '${r.name}');$('#${this.channelEditDlgId}').modal('show')"></a>
          <a href="javascript:channelRowSelected(${r.id}, ${r.cno}, '${r.name}');$('#${this.channelDeleteDlgId}').modal('show')"></a>
          <a href="javascript:channelRowSelected(${r.id}, ${r.cno}, '${r.name}');$('#${this.channelAlgConfigDlgId}').modal('show')"></a>
        `
      }
    },
    tableGetParams () {
      return {
      }
    },
    tableGetEndPoint () {
      return 'channel/channels/'
    },
    tableGetColSpecs () {
      let tableColumns = [
        {
          field: 'id',
          title: 'ID',
          visible: false
        },
        {
          field: 'cno',
          title: ''
        },
        {
          field: 'cid',
          title: 'ID'
        },
        {
          field: 'name',
          title: ''
        },
        {
          field: 'site',
          title: ''
        },
        {
          field: 'url',
          title: 'URL'
        },
        {
          field: 'status',
          title: ''
        },
        {
          field: 'operation',
          title: ''
        }
      ]
      return tableColumns
    },
    onTableSelectionChanged () {
    },
    reloadTable () {
      if (this.tableComp) {
        this.tableComp.getOnePage()
      }
      this.onTableSelectionChanged()
    },
    onPageLoaded (table, rows) {
      let endpoint = `channel/channels/get_real_time_status/?channel_ids=${rows.map(v=>v.id).join(',')}`

      console.log('onPageLoaded', rows, endpoint)

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            rows.forEach((v, i) => {
              if (!(v.id in res.data)) {
                return
              }
              this.tableComp.table.bootstrapTable('updateCell', {
                index: i,
                field: 'status',
                value: `
                  <svg class="icon svg-icon" aria-hidden="true" style='color: ${res.data[v.id] ? "green":"gray"};'>
                    <use xlink:href="#icon-dot"></use>
                  </svg>
                  ${res.data[v.id] ? "":""}
                `
              })
            })
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    rowStyle () {
      let style = {}
      return style
    },
    getDataList (res) {
      return res.data.results
    },
    getUsedFreeChannels () {
      let endpoint = 'channel/channels/get_used_free_channel_nos/'

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.freeNos = res.data.free_nos
            this.usedNos = res.data.used_nos
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    beforeModalShow () {
      this.currRowChannelId = window.currRowChannelId
      this.currRowChannelNo = window.currRowChannelNo
      this.currRowChannelName = window.currRowChannelName
      console.log('beforeModalShow', this.currRowChannelId, this.currRowChannelNo, this.currRowChannelName)
    }
  },
  mounted () {
    // If callback is defined as: function(event), then 'this' is the modal, otherwise, 'this' is this component
    $(`#${this.channelEditDlgId}`).on('show.bs.modal', (event) => {
      if ('relatedTarget' in event) {
        // Called by button comes here
        this.currRowChannelId = 0
      } else {
        // console.log('window.currRowChannelId', window.currRowChannelId)
        // this.currRowChannelId = window.currRowChannelId
        this.beforeModalShow()
      }
    })

    $(`#${this.channelDeleteDlgId}`).on('show.bs.modal', () => {
      this.beforeModalShow()
    })

    $(`#${this.channelAlgConfigDlgId}`).on('show.bs.modal', () => {
      this.beforeModalShow()
    })

    this.getUsedFreeChannels()
  }
}

</script>