<template>
  <div class="container-fluid">
    <div class="row dash-filters">
    </div>
    <div class="row dash-outline">
      <div class="col-6 px-2">
        <div class="card w-100">
          <div class="card-body py-0 px-0">
            <div class="w-100 dash-outline-algorithms">
              <div class="py-1 px-2 field-label"></div>
              <div class="py-1 px-2"> {{configuredAlgorithms}} /  {{countAlgorithms}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-6 px-2">
        <div class="card w-100">
          <div class="card-body py-0 px-0">
            <div class="w-100 dash-outline-channels">
              <div class="py-1 px-2 field-label"></div>
              <div class="py-1 px-2"> {{configuredChannels}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row my-2">
      <div class="col px-2">
        <div class="row m-0">
          <DateTimeRangePicker v-model="dateTimeRange"></DateTimeRangePicker>
        </div>
      </div>
    </div>
    <div class="row dash-lineplot">
      <div class="col">
        <div class="row">
          <div class="col px-2 channel-stats">
            <div class="card">
              <div class="card-header py-1 px-2">
                <div class="row m-0 p-0">
                  <span class="mr-4"></span>
                  <SimpleSelect
                    :itemList="['', '']"
                    label=""
                    v-model="channelStatsTimeUnit"
                  ></SimpleSelect>
                </div>
              </div>
              <div class="card-body py-1 px-2">
                <AlertChannelStatsChart :timeUnit="channelStatsTimeUnit" :dateTimeFilter="dateTimeFilter"></AlertChannelStatsChart>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row my-2"></div>
    <div class="row dash-stats">
      <div class="col-lg-3 col-md-6 px-2">
        <div class="card w-100">
          <div class="card-header py-1 px-2 dash-chart-title">
            
            <button class="btn btn-sm btn-link p-0" data-toggle="modal" :data-target="'#' + bigChartDlgId" @click="bigChartType0 = ''" style="font-size: 1.2em;">
              <svg class="icon svg-icon" aria-hidden="true">
                <use xlink:href="#icon-fangda"></use>
              </svg>
            </button>
          </div>
          <div class="card-body py-1 px-2 stats-cell">
            <AlertSiteStatsChart :dateTimeFilter="dateTimeFilter"></AlertSiteStatsChart>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-6 px-2">
        <div class="card w-100">
          <div class="card-header py-1 px-2 dash-chart-title">
            
            <button class="btn btn-sm btn-link p-0" data-toggle="modal" :data-target="'#' + bigChartDlgId" @click="bigChartType0 = ''" style="font-size: 1.2em;">
              <svg class="icon svg-icon" aria-hidden="true">
                <use xlink:href="#icon-fangda"></use>
              </svg>
            </button>
          </div>
          <div class="card-body py-1 px-2 stats-cell">
            <AlertTypeStatsChart :dateTimeFilter="dateTimeFilter"></AlertTypeStatsChart>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-6 px-2">
        <div class="card w-100">
          <div class="card-header py-1 px-2 dash-chart-title">
            
            <button class="btn btn-sm btn-link p-0" data-toggle="modal" :data-target="'#' + bigChartDlgId" @click="bigChartType0 = ''" style="font-size: 1.2em;">
              <svg class="icon svg-icon" aria-hidden="true">
                <use xlink:href="#icon-fangda"></use>
              </svg>
            </button>
          </div>
          <div class="card-body py-1 px-2 stats-cell">
            <AlertTypeStatsChartBar :dateTimeFilter="dateTimeFilter"></AlertTypeStatsChartBar>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-6 px-2">
        <div class="card w-100">
          <div class="card-header py-1 px-2 dash-chart-title">
            
            <button class="btn btn-sm btn-link p-0" data-toggle="modal" :data-target="'#' + bigChartDlgId" @click="bigChartType0 = ''" style="font-size: 1.2em;">
              <svg class="icon svg-icon" aria-hidden="true">
                <use xlink:href="#icon-fangda"></use>
              </svg>
            </button>
          </div>
          <div class="card-body py-1 px-2 stats-cell">
            <DiskUsageChart></DiskUsageChart>
          </div>
        </div>
      </div>
    </div>
  </div>
  <AlertStatsBigChartDlg :dateTimeFilter="dateTimeFilter" :modalId="bigChartDlgId" :chartType="bigChartType"></AlertStatsBigChartDlg>
</template>

<script>

import AlertTypeStatsChart from './AlertTypeStatsChart.vue'
import AlertSiteStatsChart from './AlertSiteStatsChart.vue'
import AlertTypeStatsChartBar from './AlertTypeStatsChartBar'
import AlertChannelStatsChart from './AlertChannelStatsChart'
import DiskUsageChart from './DiskUsageChart'
import SimpleSelect from '../utils/SimpleSelect'
import DateTimeRangePicker from '../utils/DateTimeRangePicker.vue'
import AlertStatsBigChartDlg from './AlertStatsBigChartDlg'

import utils from '@/utils/utils'
import apis from '@/apis/apis'
import $ from 'jquery'

export default {
  name: 'ModuleDashboard',
  components: {
    AlertSiteStatsChart,
    AlertTypeStatsChart,
    AlertTypeStatsChartBar,
    AlertChannelStatsChart,
    DiskUsageChart,
    SimpleSelect,
    DateTimeRangePicker,
    AlertStatsBigChartDlg
},
  data () {
    return {
      bigChartDlgId: utils.randomId('modalId'),
      channelStatsTimeUnit: 0,
      dateTimeRange: DateTimeRangePicker.getDefaultRange(),
      dateTimeFilter: DateTimeRangePicker.rangeToFilter(DateTimeRangePicker.getDefaultRange()),
      countAlgorithms: 0,
      configuredAlgorithms: 0,
      configuredChannels: 0,
      bigChartType0: '',
      bigChartType: ''
    }
  },
  computed: {
  },
  methods: {
    getOutlines() {
      let endpoint = `algorithm/algorithms/groupby/?by=id&count=id`
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0 && res.data) {
            this.countAlgorithms = res.data.length
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
      endpoint = `channel/channelalgorithms/groupby/?by=algorithm&count=id`
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0 && res.data) {
            this.configuredAlgorithms = res.data.length
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
      endpoint = `channel/channelalgorithms/groupby/?by=channel&count=id`
      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0 && res.data) {
            this.configuredChannels = res.data.length
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    dateTimeRange() {
      if (this.dateTimeRange) {
        this.dateTimeFilter = DateTimeRangePicker.rangeToFilter(this.dateTimeRange)
      }
    }
  },
  mounted() {
    this.getOutlines()
    $(`#${this.bigChartDlgId}`).on('shown.bs.modal', () => {
      this.bigChartType = this.bigChartType0
    })
  }
}

</script>

<style>
  .dash-outline-algorithms {
    background-color: rgb(150, 122, 220);
  }
  .dash-outline-channels {
    background-color: rgb(59, 175, 218);
  }
  /* .stats-header {
  } */
  .stats-cell {
    height: 300px;
  }
  .channel-stats {
    height: 200px;
  }
  .dash-chart-title {
    display: flex;
    justify-content: space-between;
  }
</style>
