<template>
  <div class="container-fluid">
    <div class="row w-100 ml-1 my-2">
      <DateTimeRangePicker v-model="dateTimeRange"></DateTimeRangePicker>
      <DbSelectFilter
        endpoint='channel/channels/groupby/'
        class="ml-4 mt-1"
        field="cno"
        :descent="false"
        v-model="channelNo"
        label=""
        :defaultIndex="-1"
        nullItemText=""
      ></DbSelectFilter>
      <DbSelectFilter
        endpoint='algorithm/algorithms/groupby/'
        class="ml-4 mt-1"
        field="name_ch"
        :descent="false"
        v-model="algorithm"
        label=""
        :defaultIndex="-1"
        nullItemText=""
      ></DbSelectFilter>
    </div>
    <div class="row w-100">
      <ApiDataTable
        :handlers="tableHandlers"
        tableId="alertListTableId"
        :dataHeight=400
        :showWaitingDlg='false'
        :showSearch="false"
        :detailView="false"
      ></ApiDataTable>
    </div>
    <AlertDeleteDlg
      :modalId="alertDeleteDlgId"
      :alertId="currAlertId"
      @alert-deleted="onAlertDeleted"
    ></AlertDeleteDlg>
    <AlertDetailDlg
      :modalId="alertDetailDlgId"
      :alertId="currAlertId"
    ></AlertDetailDlg>
  </div>
</template>

<script>

import ApiDataTable from '../utils/ApiDataTable'
import utils from '@/utils/utils'
import $ from 'jquery'
import apis from '@/apis/apis'
import AlertDeleteDlg from './AlertDeleteDlg'
import AlertDetailDlg from './AlertDetailDlg'
// import dayjs from 'dayjs'
import DateTimeRangePicker from '../utils/DateTimeRangePicker.vue'
import dayjs from 'dayjs'
import DbSelectFilter from '../utils/DbSelectFilter'

/**
 * 
 * @param {*} row : row data
 */
window.alertRowSelected = function (alertId) {
  window.currAlertId = alertId
}


export default {
  name: 'ModuleAlerts',
  components: {
    ApiDataTable,
    AlertDeleteDlg,
    AlertDetailDlg,
    DateTimeRangePicker,
    DbSelectFilter
},
  data () {
    return {
      alertListTableId: utils.randomId('tableId'),
      alertDeleteDlgId: utils.randomId('modalId'),
      alertDetailDlgId: utils.randomId('modalId'),
      tableComp: null,
      // For edit
      currAlertId: 0,

      dateTimeRange: DateTimeRangePicker.getDefaultRange(),

      channelNo: '',
      algorithm: ''
    }
  },
  computed: {
    tableHandlers () {
      return {
        setComponent: this.tableSetComponent,
        getEndpoint: this.tableGetEndPoint,
        getParams: this.tableGetParams,
        tableRowFormatter: this.tableRowFormatter,
        getColSpecs: this.tableGetColSpecs,
        rowStyle: this.rowStyle,
        getDataList: this.getDataList,
        onPageLoaded: this.onPageLoaded,
      }
    },
  },
  methods: {
    tableSetComponent (tableComp) {
      this.tableComp = tableComp
    },
    tableRowFormatter (r) {
      return {
        id: r.id,
        date_time: dayjs(new Date(r.date_time)).format('YYYY-MM-DDTHH:mm:ss'),
        algorithm: r.algorithm.name_ch,
        cno: r.channel.cno,
        ch_name: r.channel.name,
        ch_site: r.channel.site,
        rt_img: '',
        img: `<a href="javascript:alertRowSelected(${r.id});$('#${this.alertDetailDlgId}').modal('show')">
          <img src="${utils.getImageUrl(r.img)}" style="max-height: 50px;max-width: 80px;">
          </a>
        `,
        operation: `
          <a href="javascript:alertRowSelected(${r.id});$('#${this.alertDeleteDlgId}').modal('show')"></a>
        `
      }
    },
    tableGetParams () {
      let params = {
        ordering: '-date_time'
      }
      if (this.dateTimeRange) {
        params = {...params, ...DateTimeRangePicker.rangeToFilter(this.dateTimeRange)}
      }

      if (this.channelNo) {
        params['channel__cno'] = this.channelNo
      }
      
      if (this.algorithm) {
        params['algorithm__name_ch'] = this.algorithm
      }
      return params
    },
    tableGetEndPoint () {
      return 'alert/alerts2/'
    },
    tableGetColSpecs () {
      let tableColumns = [
        {
          field: 'id',
          title: 'ID'
        },
        {
          field: 'date_time',
          title: ''
        },
        {
          field: 'algorithm',
          title: ''
        },
        {
          field: 'cno',
          title: ''
        },
        {
          field: 'ch_name',
          title: ''
        },
        {
          field: 'ch_site',
          title: ''
        },
        {
          field: 'rt_img',
          title: ''
        },
        {
          field: 'img',
          title: ''
        },
        {
          field: 'operation',
          title: ''
        }
      ]
      return tableColumns
    },
    onTableSelectionChanged () {
    },
    reloadTable () {
      if (this.tableComp) {
        this.tableComp.getOnePage()
      }
      this.onTableSelectionChanged()
    },
    onPageLoaded (table, rows) {
      let endpoint = `alert/alerts/get_real_time_image/?alert_ids=${rows.map(v=>v.id).join(',')}`

      console.log('onPageLoaded', rows, endpoint)

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            rows.forEach((v, i) => {
              if (!(v.id in res.data)) {
                return
              }
              this.tableComp.table.bootstrapTable('updateCell', {
                index: i,
                field: 'rt_img',
                value: `<a href="javascript:alertRowSelected(${v.id});$('#${this.alertDetailDlgId}').modal('show')">
                  <img src="${utils.getImageUrl(res.data[v.id])}" style="max-height: 50px;max-width: 80px;">
                  </a>
                `
              })
            })
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    rowStyle () {
      let style = {}
      return style
    },
    getDataList (res) {
      return res.data.results
    },
    beforeModalShow () {
      console.log('beforeModalShow')
      this.currAlertId = window.currAlertId
    },
    onAlertDeleted () {
      this.reloadTable()
    }
  },
  watch: {
    dateTimeRange () {
      console.log('dateTimeRange changed', this.dateTimeRange)
      this.reloadTable()
    },
    channelNo () {
      console.log('channelNo changed', this.channelNo)
      this.reloadTable()
    },
    algorithm () {
      console.log('algorithm changed', this.algorithm)
      this.reloadTable()
    }
  },
  mounted () {
    $(`#${this.alertDeleteDlgId}`).on('show.bs.modal', () => {
      this.beforeModalShow()
    })
    $(`#${this.alertDetailDlgId}`).on('show.bs.modal', () => {
      this.beforeModalShow()
    })
    // console.log('ModuleAlerts mounted', process.env, this)
  }
}

</script>