<template>
  <SimpleModalDlg
    :modalId="modalId"
    :dialogWidth="1000"
    :title="!alertRec ? '' : ` ${alertRec.channel.cno} - ${alertRec.algorithm.name_ch}`"
    defaultButtonText=""
  >
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <div class="row">
          <div class="col w-100">
            {{!alertRec ? '' : formatDateTime(alertRec.date_time)}}
          </div>
        </div>
        <div class="row">
          <div class="col w-100">
            {{!alertRec ? '' : alertRec.channel.site}}
          </div>
        </div>
        <div class="row mt-2">
          <div class="col-6 text-center">
            <img :src="rtImgUrl" alt="" style="max-width: 100%;">
          </div>
          <div class="col-6 text-center">
            <img :src="imgUrl" alt="" style="max-width: 100%;">
          </div>
        </div>
        <div class="row">
          <div class="col-6 text-center">
            <label></label>
          </div>
          <div class="col-6 text-center">
            <label></label>
          </div>
        </div>
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'
import dayjs from 'dayjs'

export default {
  name: 'AlertDetailDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
      alertRec: null,
      imgUrl: null,
      rtImgUrl: null
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    alertId: {
      type: Number,
      required: true
    },
  },
  computed: {
  },
  methods: {
    formatDateTime (dt) {
      return dayjs(new Date(dt)).format('YYYYMMDD HH:mm:ss')
    },
    loadRtImage () {
      if (!this.alertId) {
        return
      }

      let endpoint = `alert/alerts/get_real_time_image/?alert_ids=${this.alertId}`

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            this.rtImgUrl = utils.getImageUrl(res.data[this.alertId])
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    loadRecord () {
      if (!this.alertId) {
        return
      }
      let endpoint = `alert/alerts2/${this.alertId}/`

      let data = {}

      apis.apiAjaxGet(endpoint, data,
        (res) => {
          console.log(res.data)
          this.alertRec = res.data
          this.imgUrl = utils.getImageUrl(this.alertRec.img)
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    alertId () {
      this.loadRecord()
      this.loadRtImage()
    }
  },
  mounted () {
  }
}
</script>
